@import url('https://fonts.googleapis.com/css?family=Roboto:300,300i,400,500,700,900');
@import url('https://fonts.googleapis.com/css?family=PT+Sans+Narrow:400,700');

body {
  font-family: 'Roboto', sans-serif;
}
.titleBar {
  background-color:  #000;
  display: block;
  color: #fff;
  padding-left: 1rem;
  font-size: 2rem;
}

.searchBar {
  font-size: 1.5rem;
  display: "block";
  padding: 1rem;

}

.App {
  /* text-align: center; */
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}


.movie__item {
    display: -ms-flexbox;
    display: flex;
    border: 1px solid #ccc;
    /* padding: 1rem; */

    @media(max-width: 480px) {
      -ms-flex-direction: column;
          flex-direction: column;
    }
}

.movie__item_poster {
    /* padding: 0.5rem; */
}

.movie__item_poster img {
  width: 100%;
  height: 100%;
}

.movie__item_info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    /* background: #5cdb95; */
}

.movie__item_moviename {
    font-size: 2rem;
    text-transform: uppercase;
    background: #05386b;
    font-family: 'PT Sans Narrow', sans-serif;
    padding: 10px;
    color: #edf5e1;
}

input[type="button"] {
    width: 10rem;
    padding: 1rem;
    background: #b32222;
    border-radius: 25px;
    color: white;
    font-size: 1rem;
}

input[type="button"]:hover {
    background: #d72525;
    color: #ccc;
}
.movie__item_description {
    padding: 1rem;
}

.movie__item_button {
  margin: 0 auto;
}



